@import "../../../common/styles/mixins";
@import "../../../common/styles/variables";
@import "../../../common/styles/layouts";

.content__img {
  width: 100%
}

.profileInfoContainer {
  grid-area: profile-description;
  @include flexColumn();

  .mainProfileBox {
    @include flexColumn();
    @extend .blocksBorder;
    justify-content: space-between;
    align-items: center;
    background-color: $blocksBackground;
    margin-bottom: 16px;
    padding: 24px 16px;

    .profilePhoto {
      width: 180px;
      height: 180px;
      border-radius: 50%;
      margin-top: 8px;
    }

    .inputtypeFile {
      opacity: 0;
      position: absolute;
      z-index: -1;
    }

    .uploadImage {
      background: none;
      border: none;
      padding: 0;
      font: inherit;
      cursor: pointer;
      outline: inherit;
      margin-top: 10px;
    }
  }

  .settingButtons {
    text-align: center;
    margin-bottom: 10px;

    .settingButton {
      background: none;
      border: none;
      padding: 0;
      font: inherit;
      cursor: pointer;
      outline: inherit;
      color: #007bff;
      font-weight: 600;

      &:hover {
        color: darkblue;
      }
    }
  }

  .restProfileInformation {
    background-color: $blocksBackground;
    @extend .blocksBorder;
    .profileDataContainer {
      & > div:not(div:last-child) {
        padding: 16px;
        border-bottom: 1.5px solid #eaebec;
      }

      .aboutMe {
        @include flexColumn();
        align-items: center;
      }

      .contact {
        padding: 16px;
        border-bottom: 2px solid #eaebec;
      }
    }

  }

  .editProfileInformation {
    @extend .blocksBorder;
    & > div:not(:first-child, :last-child) {
      padding: 16px;
      border-top: 2px solid #eaebec;
    }
    & > div:first-child {
      padding: 16px;
    }

    & > div:not(div:first-child) {
      @include flexColumn();
      text-align: center;
    }

    .contactForm {
      padding: 16px;
      border-bottom: 2px solid #eaebec;
    }
    .profileEditStatus {
      color: #ff0000;
      border: 1px solid #ff0000;
      border-radius: 5px;
      position: relative;
      z-index: 1;
    }
  }
}

.saveButtons {
  text-align: center;
  //margin-bottom: 10px;

  .saveButton {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: #007bff;
    font-weight: 600;

    &:hover {
      color: darkblue;
    }
  }
}







